import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path:'/',
        redirect:'/home'
    },
    {
        path:"/home",
        component:()=>import("../views/Home")
    },
    {
        path:"/city/:cityId",
        name:"c",
        component:()=>import("../views/City")
    },
    {
        path:"",
        component:()=>import("../views/MainPage"),
        children:[
            {path:"msite",component:()=>import("../components/Msite")},
            {path:"search",component:()=>import("../components/Search")},
            {path:"order",component:()=>import("../components/Order")},
            {path:"profile",component:()=>import("../components/Profile")}
        ]

    },
    {
        path:"/login",
        component:()=>import("../views/Login")
    },
    {
        path:"/food",
        component:()=>import("../components/Food")
    },
    {
        path:"/shop",
        component:()=>import("../components/Shop"),
        children: [
            {
                path:"",
                redirect: "commodity"
            },
            {
                path:"commodity",
                component:()=>import("../components/Commodity")
            },
            {
                path:"evaluate",
                component:()=>import("../components/Evaluate")
            }
        ]
    },
    {
        path:"/balance",
        component:()=>import("../components/Balance"),
        children:[
            {
                path:"detail",
                component:()=>import("../components/Detail")
            }
        ]
    },
    {
        path:"/points",
        component:()=>import("../components/Points"),
        children:[
            {
                path:"detail",
                component:()=>import("../components/Detail")
            }
        ]
    }
]

const router = new VueRouter({
    mode:'hash',
    routes
})

export default router
